// Variable and common properties for all application style

$fontColorGreen: #2bb673;
$fontColorGrey:#c4c4c4;


@mixin transitionMaxHeight($time) {
  -webkit-transition: max-height $time ease-in-out;
  -moz-transition: max-height $time ease-in-out;
  -o-transition: max-height $time ease-in-out;
  transition: max-height $time ease-in-out;
}

@mixin transitionAll($time) {
  -webkit-transition: all $time ease-in-out;
  -moz-transition: all $time ease-in-out;
  -o-transition: all $time ease-in-out;
  transition: all $time ease-in-out;
}

@mixin zoom($rate){
  -moz-transform: scale($rate);
  -webkit-transform: scale($rate);
  transform: scale($rate);
}

//=========== FONTS
@font-face {
  font-family: FuturaPtMedium;
  src: url('./fonts/FuturaPt/FuturaPTMedium.otf');
}

@font-face {
  font-family: FuturaPTLight;
  src: url('./fonts/FuturaPt/FuturaPTLight.otf');
}

// ================ COMMON

*{
  font-family: FuturaPtMedium;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 0px;
  line-height: 1.6em;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #ffffff;
}

body {
  margin: 0;
  padding: 0;
  min-width: 320px;

  //font-family: sans-serif;

}

.uppercase {
  text-transform: uppercase;
}

a {
  text-decoration: none;
}

a:-webkit-any-link {
  color: $fontColorGreen;
  cursor: auto;
  text-decoration: none;
}

ul{
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
  -webkit-padding-start: 0px;
}

h1{
  font-family: "Source Sans Pro",sans-serif;
  font-size: 2em;
  font-weight: 600;
  letter-spacing: 2px;
  font-family: Open Sans;
  font-size: 22px;
  font-weight: 400;
  text-transform: uppercase;
  font-style: normal;
  letter-spacing: 0px;
  text-decoration: none;
  line-height: 1.2em;
  padding: 0 0 25px 0;
  width: 100%;
  text-align: center;
}

h2{
  font-family: "Source Sans Pro",sans-serif;
  font-size: 1.4em;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: Open Sans;
  font-size: 19px;
  font-weight: 700;
  text-transform: none;
  font-style: normal;
  letter-spacing: .4px;
  text-decoration: none;
  line-height: 1.2em;
  width: 100%;
  text-align: center;
  padding-top: 25px;
  padding-bottom: 25px;
}

.block624 {
  display: flex;
  max-width: 624px;
  min-width: 320px;
  padding: 0 15px 0 15px;
  min-width: 320px;
  flex-wrap: wrap;
  width: 60vw;
}

.block1000 {
  display: flex;
  max-width: 1000px;
  padding: 0 15px 0 15px;
  min-width: 320px;
  flex-wrap: wrap;
  width: 100%;
}

.blockFullWidth{
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.component__wrapper {
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  overflow: auto;
  flex-wrap: wrap;
  z-index: 2000;
  width: 100%;
}

p, li{
  font-family: "Open Sans";
  font-size: 1.1em;
  font-weight: 500;
  position: relative;
  width: 100%;
  text-align: left;
  line-height: 1.6em;
}

